@import url("./Nullify.scss");
@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@400;700&family=Barlow:ital@1&family=Bellefair&display=swap");

body {
  font-family: "Barlow Condensed";
  background-color: black;
}
._container {
  max-width: 1415px;
  padding: 0px 15px;
  margin: 0 auto;
}

.App {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
main {
  width: 100%;
  height: 100%;
}

// HEADER
header {
  width: 100%;
  padding-top: 40px;

  @media (max-width: 1023.98px) {
    padding-top: 0px;
  }
  @media (max-width: 767.98px) {
    padding-top: 24px;
  }
  .header_container {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;

    .header_body {
      position: relative;
      &::after {
        content: "";
        width: 40%;
        height: 1px;
        background: #ffffff;
        position: absolute;
        top: 49%;
        z-index: 5;
        left: 8%;
        opacity: 0.25;
        @media (max-width: 1414.98px) {
          width: 37%;
        }
        @media (max-width: 1214.98px) {
          width: 36%;
        }
        @media (max-width: 1023.98px) {
          width: 35%;
        }
        @media (max-width: 767.98px) {
          display: none;
        }
      }
      padding: 24px 0px;
      @media (max-width: 1023.98px) {
        padding: 0px 0px;
      }
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .header-logo {
        @media (max-width: 767.98px) {
          position: fixed;
          top: 24px;
          left: 24px;
        }
      }
      .header-links {
        &::before {
          content: "";
          background: rgba(255, 255, 255, 0.04);
          backdrop-filter: blur(40.7742px);
          position: absolute;
          width: 400%;
          height: 100%;
          top: 0;
          left: 0;
          z-index: -1;
          @media (max-width: 767.98px) {
            z-index: 10;
          }
        }
        z-index: 5;
        display: flex;
        position: relative;
        transition: all 0.3s;
        align-items: center;

        width: 67.6%;
        max-width: 830px;

        margin-right: -5.5rem;

        padding: 2rem 10.4375rem 0rem 7.6875rem;
        @media (max-width: 1023.98px) {
          padding: 2rem 8.4375rem 0rem 6.6875rem;
        }
        @media (max-width: 991.98px) {
          padding: 2rem 5.4375rem 0rem 3rem;
        }

        @media (max-width: 767.98px) {
          position: fixed;
          width: 75%;
          right: -100%;
          max-width: none;
          z-index: 10;
          &.burger-active {
            right: 0;
          }

          justify-content: flex-start;
          padding-top: 35%;
          flex-direction: column;
          align-items: flex-start;

          gap: 10px;
          margin: 0;

          top: -24px;
          &.header-link {
            font-size: 16px;
            padding: 5px;
          }
          &::before {
            height: 200vh;
            width: 100vh;
            z-index: -1;
          }
        }

        justify-content: space-between;
        align-items: center;
        .header-link {
          &:not(:last-child) {
            margin-right: 1rem;
          }
          font-weight: 400;
          font-size: 16px;
          line-height: 118%;
          letter-spacing: 2.7px;
          padding-bottom: 2.6rem;
          padding-top: 0.7rem;

          border-bottom: solid 3px #ffffff00;
          transition: all 0.3s;
          color: #ffffff;
          transition: all 0.1s;
          @media (max-width: 767.98px) {
            width: 100%;
          }

          & span {
            font-weight: 700;
          }
          &:hover,
          &:focus,
          &.active-link,
          &:active {
            border-bottom: solid 3px #fff;
          }
        }
      }
    }
  }
}
.burger-icon {
  width: 24px;
  height: 21px;
  position: fixed;
  top: 34px;
  right: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  cursor: pointer;
  & span {
    width: 100%;
    height: 3px;
    background-color: #d0d6f9;
  }
  &::before,
  &::after {
    content: "";
    width: 100%;
    transition: all 0.3s;
    height: 3px;
    position: absolute;
    left: 0;
    background-color: #d0d6f9;
  }
  &::before {
    top: 0;
  }
  &::after {
    bottom: 0;
  }
  &.active {
    &::before {
      transform: rotate(45deg);
      top: 9px;
    }
    &::after {
      transform: rotate(-45deg);
      bottom: 9px;
    }
    & span {
      background-color: #ffffff00;
    }
  }
}

// HOME
.home {
  padding: 30.1% 11.8% 10.4% 11.8%;
  width: 100%;
  height: 100%;
  @media (max-width: 1200px) {
    padding: 30.1% 8.8% 9.4% 8.8%;
  }

  @media (max-width: 767.98px) {
    padding: 12rem 1.5rem 1.5rem 1.5rem;
  }
  @media (max-width: 523.98px) {
    padding: 7rem 1.5rem 1.5rem 1.5rem;
  }
  .home-bg {
    position: fixed;
    background: url("./assets/home/background-home-desktop.jpg") 100% no-repeat;
    width: 100%;
    height: 100%;
    background-size: 100% 100%;

    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -2;
    @media (max-width: 1023.98px) {
      background: url("./assets/home/background-home-tablet.jpg") 100% no-repeat;
      background-size: 100% 100%;

      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
    @media (max-width: 523.98px) {
      background: url("./assets/home/background-home-mobile.jpg") 100% no-repeat;
      background-size: 100% 100%;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}
.home-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 1023.98px) {
    flex-direction: column;
  }
}
.home-content {
  display: flex;
  z-index: 1;
  position: relative;
  display: flex;
  flex-direction: column;
}
.home_subtitle {
  margin-bottom: 24px;
  z-index: 1;
}
.subtitle {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 121%;

  letter-spacing: 4.725px;

  color: #d0d6f9;
  @media (max-width: 1023.98px) {
    font-size: 20px;
    text-align: center;
  }
  @media (max-width: 523.98px) {
    font-size: 16px;
  }
}
.home_title {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  z-index: 1;
  font-size: 150px;
  line-height: 114%;
  color: #ffffff;
  margin-bottom: 24px;
  @media (max-width: 1023.98px) {
    font-size: 132px;
    text-align: center;
  }
  @media (max-width: 523.98px) {
    font-size: 80px;
    margin-bottom: 16px;
  }
}

.home-text {
  max-width: 444px;
  @media (max-width: 1023.98px) {
    font-size: 16px;
    z-index: 1;
    text-align: center;
  }
  @media (max-width: 523.98px) {
    font-size: 15px;
  }
}
.text {
  font-family: "Barlow";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 177%;
  /* or 178% */

  color: #d0d6f9;
}
.home-button {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 115%;
  flex: 0 0 274px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 274px;
  margin-top: 6rem;
  /* identical to box height */

  letter-spacing: 2px;

  color: #0b0d17;
  @media (max-width: 1023.98px) {
    flex: 0 0 244px;
    width: 244px;
    margin-top: 9.4rem;
  }
  @media (max-width: 523.98px) {
    flex: 0 0 150px;
    width: 150px;
    margin-top: 5rem;
    font-size: 20px;
  }
}

// DESTINATION
