  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 121%;
  letter-spacing: 4.725px;
  text-transform: uppercase;

  color: #ffffff;
  margin-bottom: 97px;
  @media (max-width: 1023.98px) {
    margin-left: -4.1875rem;
  }
  @media (max-width: 767.98px) {
    margin-left: -4.1875rem;
    margin-top: 40px;
    font-size: 20px;
  }
  @media (max-width: 523.98px) {
    margin-left: -0rem;
