}
.destination-image {
  width: 445px;
  height: 445px;
  position: relative;
  @media (max-width: 1023.98px) {
    align-self: center;
  }
  @media (max-width: 767.98px) {
    width: 300px;
    height: 300px;
  }
  @media (max-width: 523.98px) {
    width: 170px;
    height: 170px;
  }
  & img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}
.dest-body {
  & h2 {
    font-family: "Bellefair";
    font-style: normal;
    font-weight: 400;
    font-size: 100px;
    text-transform: uppercase;
    line-height: 115%;
    @media (max-width: 767.98px) {
      font-size: 80px;
      text-align: center;
    }
    @media (max-width: 523.98px) {
      font-size: 56px;
    }

    color: #ffffff;
    margin-bottom: 14px;
  }
  & .dest-descr {
    font-family: "Barlow";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 178%;
    max-width: 444px;
    padding-bottom: 54px;
    border-bottom: solid 1px #383b4b;

    color: #d0d6f9;
    margin-bottom: 28px;
    @media (max-width: 1023.98px) {
      text-align: center;
    }
    @media (max-width: 767.98px) {
      font-size: 16px;
    }
    @media (max-width: 523.98px) {
      font-size: 15px;
    }
  }
}
.dest-nav {
  width: 100%;
  height: 100%;
  max-height: 34px;
  margin-bottom: 37px;
  & ul {
    width: 100%;
    height: 100%;
    display: flex;
    max-height: 34px;
    & li {
      display: inline-block;
      width: 100%;
      height: 100%;
      max-height: 19px;
      & a {
        display: inline;
        font-family: "Barlow Condensed";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 119%;
        letter-spacing: 2.7px;
        color: #ffffff;
        max-height: 19px;
        padding-bottom: 12px;
        border-bottom: solid 3px #ffffff00;
        @media (max-width: 523.98px) {
          font-size: 14px;
        }
        &:hover,
        &:active,
        &:focus,
        &.active-dest-link {
          border-bottom: solid 3px #ffffff;
