    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    letter-spacing: 2.3625px;
    text-transform: uppercase;

    color: #d0d6f9;
    margin-bottom: 12px;
    @media (max-width: 523.98px) {
      text-align: center;
    }
  }
  & .av-dist-text {
    font-family: "Bellefair";
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 114%;
    text-transform: uppercase;

    color: #ffffff;
    @media (max-width: 523.98px) {
      text-align: center;
    }
  }
}
.planet-av-time {
  & .av-time-title {
    font-family: "Barlow Condensed";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    letter-spacing: 2.3625px;
    text-transform: uppercase;
    @media (max-width: 523.98px) {
      text-align: center;
    }
    color: #d0d6f9;
    margin-bottom: 12px;
  }
  & .av-time-text {
    font-family: "Bellefair";
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 114%;
    @media (max-width: 523.98px) {
      text-align: center;
    }
