      right: 0;
      bottom: 0;
      z-index: -2;
    }
    @media (max-width: 523.98px) {
      background: url("./assets/technology/background-technology-mobile.jpg")
        100% no-repeat;
      background-size: 100% 100%;

      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -2;
    }
  }
}

.tech_container {
  display: flex;
  flex-direction: column;
  padding: 4.75rem 0rem 6rem 10rem;
  @media (max-width: 1414.98px) {
    padding: 4.75rem 0rem 6rem 1rem;
  }
  @media (max-width: 1023.98px) {
    justify-content: center;
    align-items: center;
    padding: 0;
  }
}
.tech_title {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 121%;

  letter-spacing: 4.725px;

  color: #ffffff;
  & span {
    font-weight: 700;
    opacity: 0.25;
  }
  @media (max-width: 1023.98px) {
    margin-top: 120px;
    padding-left: 2rem;
    margin-bottom: 60px;
  }
  @media (max-width: 767.98px) {
    font-size: 20px;
  }
  @media (max-width: 523.98px) {
    font-size: 16px;
  }
}
.tech_body {
  display: flex;
  gap: 130px;
  width: 100%;
  height: 100%;
  @media (max-width: 1414.98px) {
    gap: 100px;
  }
  @media (max-width: 1199px) {
    gap: 40px;
  }
  @media (max-width: 1023.98px) {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 458px;
  }
}
.tech_content {
  display: flex;
  gap: 80px;
  width: 100%;
  @media (max-width: 523.98px) {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}
.tech_pagination {
  display: flex;
  gap: 32px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  @media (max-width: 1023.98px) {
    flex-direction: row;
    margin-top: 54px;
    margin-bottom: 44px;
  }
}
.tech_pag {
  width: 80px;
  height: 80px;
  @media (max-width: 767.98px) {
    width: 60px;
    height: 60px;
  }
  @media (max-width: 523.98px) {
    width: 40px;
    height: 40px;
  }
  border: solid 1px #ffffff3c;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Bellefair";
  font-weight: 400;
  font-size: 32px;
  line-height: 115%;
  text-align: center;
  letter-spacing: 2px;
  color: #fff;
  cursor: pointer;
  transition: all 0.2s;
}
.active-tech-pag,
.tech_pag:hover {
  background-color: #fff;
  color: #0b0d17;
}
.tech_conent-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tech_subtitle {
  font-family: "Barlow Condensed";
  font-weight: 400;
  font-size: 16px;
  line-height: 119%;
  letter-spacing: 2.7px;
  color: #d0d6f9;
  margin-bottom: 11px;
  @media (max-width: 1023.98px) {
    text-align: center;
  }
  @media (max-width: 523.98px) {
    font-size: 14px;
  }
}
.tech_main-title {
  font-family: "Bellefair";
  font-weight: 400;
  font-size: 56px;
  line-height: 114%;
  color: #ffffff;
  text-transform: uppercase;
  margin-bottom: 17px;
  width: 470px;
  @media (max-width: 1023.98px) {
    text-align: center;
  }
  @media (max-width: 767.98px) {
    font-size: 40px;
  }
  @media (max-width: 523.98px) {
    font-size: 24px;
  }
}
.tech_text {
  font-family: "Barlow";
  font-weight: 400;
  font-size: 18px;
  line-height: 178%;
  color: #d0d6f9;
  @media (max-width: 1023.98px) {
    text-align: center;
  }
  @media (max-width: 767.98px) {
    font-size: 16px;
  }
  @media (max-width: 523.98px) {
    font-size: 15px;
  }
}
.tech_image {
  width: 100%;
  height: 100%;

  & img {
    max-height: 527px;
    width: 100%;
    height: 100%;
    object-fit: contain;

    object-position: center;
  }
}
